import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '../views/Homepage.vue'
import Admin from '../views/admin/Admin.vue'
import FoodManagePage from '../views/admin/page/FoodManagePage.vue'
import UserInfo from '../views/user/page/UserInfoPage.vue'
import UserModify from '../views/user/page/Modify.vue'
import SignPage from '../views/user/page/SignPage.vue'
import LoginPage from '../views/user/page/LoginPage.vue'
import Review from '../components/Review.vue'
import ReBoard from '../components/ReBoard.vue'
import BoardDetail from '../components/BoardDetail.vue'
import BoardModify from '../components/BoardModify'
import MenuPage from '../views/user/page/MenuPage.vue'
import UserPage from '../views/user/UserPage.vue'
import UserMyPage from '../views/user/page/UserMyPage.vue'
import ShopInfo from '../views/user/page/ShopInfo.vue'
import store from '../store'
import NullImageManagePage from './admin/page/NullImageManagePage'
import SlideManagePage from './admin/page/SlideManagePage.vue'
import OrderPage from './admin/page/OrderPage.vue'
import MyArticlePage from '../views/user/page/MyArticlePage.vue';
import UserManagePage from './admin/page/UserManagePage.vue'

const beforeAuth = isAuthenticated => (from, to, next) => {
  const isAuth = store.getters['user/isAuth'];
  if ((isAuth && isAuthenticated) || (!isAuth && !isAuthenticated)) {
    return next();
  } else {
    alert("  .");
    next('/user/login');
  }
}


const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage,
  },
  {
    path: '/review',
    name: 'review',
    component: Review,
    meta: { metaName: ''}
  },
  {
    path: '/board',
    name: 'board',
    component: ReBoard,
    meta: { metaName: ''}
  },

  {
    path: '/user',
    name: 'user',
    component: UserPage,
    children: [
      {
        path: 'myarticle',
        name: 'myarticle',
        component: MyArticlePage,
        meta: { metaName: ' '}
      },
      {
        path: 'review/:id',
        name: 'review',
        component: Review,
        meta: { metaName: ' '}
      },
      {
        path: 'board',
        name: 'board',
        component: ReBoard,
        meta: { metaName: ''}
      },
      {
        path: 'board/:id',
        name: 'boardItem',
        component: BoardDetail,
        meta: { metaName: ' '}
      },
      {
        path: 'board/modify/:id',
        name: 'boardModify',
        component: BoardModify,
        meta: { metaName: ' '}
      },
      {
        path: 'sign',
        name: 'sign',
        component: SignPage,
        meta: { metaName: ''}
      },
      {
        path: 'login',
        name: 'login',
        component: LoginPage,
        meta: { metaName: ''}
      },
      {
        path: 'menu',
        name: 'menu',
        component: MenuPage,
        meta: { metaName: ''}
      },
      {
        path: 'userinfo',
        name: 'userinfo',
        component: UserInfo,
        beforeEnter: beforeAuth(true),
        meta: { metaName: ' '}
      },
      {
        path: 'modify',
        name: 'modify',
        component: UserModify,
        meta: { metaName: '  '}
      },
      {
        path: 'mypage',
        name: 'mypage',
        component: UserMyPage,
        meta: { metaName: ' '}
      },
      {
        path: 'shopinfo',
        name: 'shopinfo',
        component: ShopInfo,
        meta: { metaName: ' ' }
      }
    ]
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta: { RootName: 'admin' },
    //  admin   meta    nav  
    //  Homepage    view user ,
    //  nav     .
    //  -> app.js   meta data   
    children: [
      {
        path: 'nullImage',
        name: 'nullImage',
        component: NullImageManagePage,
      },
      {
        path: 'foodManagement',
        name: 'foodManagement',
        component: FoodManagePage,
      },
      {
        path: 'SlideImage',
        name: 'SlideImage',
        component: SlideManagePage,
      },
      {
        path: 'order',
        name: 'order',
        component: OrderPage,
      },
      {
        path: 'userManagement',
        name: 'userManagement',
        component: UserManagePage
      }
    ]
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  }
})

export default router